body {
  box-sizing: border-box;
  --link-active: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style-type: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__menu-list {
  align-items: center;
  display: flex;
}

.menu__list-item {
  margin-right: 20px;
}

.menu__list-item:last-child {
  margin-right: 0;
}

.menu__list-link {
  color: #000;
  line-height: 60px;
  display: block;
}

.menu__list-link:hover {
  color: var(--link-active);
  cursor: pointer;
}

.is-active {
  color: var(--link-active);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

